
@font-face {
  font-family: 'Raleway';
  src: url('/static/fonts/Raleway-Regular.woff') format('woff');
}
#header {
  background-color: #dcf4f3;
  grid-column: 1 / 6;
  padding-left: 2.5rem;
}
#app {
  font-family: 'Raleway', sans-serif;
  color: #0E2B29;
  display: grid;
  grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
}
h1 {
  font-size: 2.5rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.75rem;
}
input {
  background: #fff;
  padding: .5Rem;
  border: 1px #ddd solid;
  width: 10rem;
  margin-bottom: 1rem;
}
.btn {
  margin: 0;
  background: #FC913a;
  padding: .5rem;
  border: 1px #FC913a solid;
  color: #fff;
  font-weight: 400;
  letter-spacing: .15em;
  margin-bottom: 1rem;
}
button {
  border: none;
  background: none;
}
button:hover {
  cursor: pointer
}
#menu-btn {
  float: right;
  margin: 2rem 2.5rem 0 0;
  height: 3rem;
  width: 3rem;
}
.input-danger {
  color: #FF6B6B;
  font-weight: bold;
  padding-bottom: .5rem;
}
.raised {
  -webkit-box-shadow: 1px 2px 3px #ccc;
          box-shadow: 1px 2px 3px #ccc;
  -webkit-transition: -webkit-transform .3s ease;
  transition: -webkit-transform .3s ease;
  transition: transform .3s ease;
  transition: transform .3s ease, -webkit-transform .3s ease;
}
.fade-leave-active, .fade-enter-active{
  -webkit-transition: opacity .2s ease;
  transition: opacity .2s ease;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
.popin-leave-active, .popin-enter-active {
  -webkit-transition: all .2s ease-in;
  transition: all .2s ease-in;
}
.popin-enter, .popin-leave-to {
  opacity: 0;
  -webkit-transform: scale(2);
          transform: scale(2);
  fill: green;
}
.slidein-enter, .slidein-leave-to {
  -webkit-transform: translateX(20rem);
          transform: translateX(20rem);
}
.slidein-leave-active, .slidein-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}


